export default {
	data() {
		return {
			result:[],
			data:{},
			checkArr:[],
			checkValue:[],
			pickVal:[],
			showPicker:false,
		};
	},
	computed:{
		
	},
	props:{
		mode:{
			type:String,
			default(){
				return ""
			}
		},
		themeColor:{
			type:String,
			default(){
				return "#f00"
			}
		},
		defaultVal:{
			type:String,
			default(){
				return ""
			}
		},
		step:{
			type:null,
			default:1
		},
		current:{
			type:Boolean,
			default:false
		},
		selectList:{
			type:Array,
			default(){
				return [];
			}
		}
	},
	watch:{
		mode(){
			this.initData();
		}
	},
	methods:{
		maskTap(){
			this.showPicker = false;
		},
		show(){
			this.showPicker = true;
		},
		hide(){
			this.showPicker = false;
		},
		pickerCancel(){
			this.$emit("cancel",{
				result:''
			});
			this.showPicker = false;
		},
		pickerConfirm(e){
			let _this = this;
			let index = _this.pickVal.length > 0 ? _this.pickVal[0] : 0;
			let result = _this.data[index];
			_this.$emit("confirm",result);
			_this.showPicker = false;
		},
		bindChange(val){
			let _this=this;
			let arr = val.detail.value;
			// console.log(arr);
			// console.log(_this.data);
			_this.$nextTick(()=>{
				_this.pickVal= arr;
			})
		},
		initData(){
			let _this=this;
			// console.log(_this)
			_this.data = _this.selectList;
			_this.$nextTick(()=>{
				_this.pickVal= [];
			})
		}
	},
	mounted() {
		this.initData();
	}
}
